.container {
    max-height: 850px;
    overflow: hidden;
}
.container .message {
    font-size: 55px;
    padding-top: 200px;
}
.game {
    background: rgba(238, 228, 218, 0.5);
    text-align: center;
    border-radius: 10px;
    font-size: 55px;
    font-weight: bold;
    margin: 5% auto;
}

.game td {
    width: 25%;
    height: 25%;
    border-radius: 10px;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
}

.text-box {
    position: relative;
    background: rgba(238, 228, 218, 0.5);
    margin: auto;
    width: 778px;
    height: 778px;
    text-align: center;
    top: -833px;
}

@media (min-device-width: 768px) {
    .game td {
        width: 180px;
        height: 180px;
    }
}

@media (max-device-width: 768px) {
    .game td {
        width: 90px;
        height: 90px;
    }
}

.tile-0 {
    background: rgba(238, 228, 218, 0.35);
    color: rgba(238, 228, 218, 0);
}

.tile-2 {
    background: #eee4da;
}

.tile-4 {
    background: #ede0c8;
}

.tile-8 {
    color: #f9f6f2;
    background: #f2b179;
}

.tile-16 {
    color: #f9f6f2;
    background: #f59563;
}

.tile-32 {
    color: #f9f6f2;
    background: #f67c5f;
}

.tile-64 {
    color: #f9f6f2;
    background: #f65e3b;
}

.tile-128 {
    color: #f9f6f2;
    background: #edcf72;
    font-size: 45px;
}

@media screen and (max-width: 520px) {
    .tile.tile-128 .tile-inner {
        font-size: 25px;
    }
}

.tile-256 {
    color: #f9f6f2;
    background: #edcc61;
    font-size: 45px;
}

@media screen and (max-width: 520px) {
    .tile.tile-256 .tile-inner {
        font-size: 25px;
    }
}

.tile-512 {
    color: #f9f6f2;
    background: #edc850;
    font-size: 45px;
}

@media screen and (max-width: 520px) {
    .tile.tile-512 .tile-inner {
        font-size: 25px;
    }
}

.tile-1024 {
    color: #f9f6f2;
    background: #edc53f;
    font-size: 35px;
}

@media screen and (max-width: 520px) {
    .tile.tile-1024 .tile-inner {
        font-size: 15px;
    }
}

.tile-2048 {
    color: #f9f6f2;
    background: #edc22e;
    font-size: 35px;
}

@media screen and (max-width: 520px) {
    .tile.tile-2048 .tile-inner {
        font-size: 15px;
    }
}



